import { instance } from './index';

const kakaoRequestData = {
  kakaoUrl: 'https://kauth.kakao.com/oauth/',
  apiKey: 'a8f9e62ec6d2c5b6507b32000932a97f',
  redirectUrl: 'http://vue-til.s3-website.ap-northeast-2.amazonaws.com/main',
};

// 회원가입 API
const registerUser = userData => instance.post('signup', userData);

// 로그인 API
const loginUser = loginData => instance.post('login', loginData);

// 카카오 로그인
const kakaoLogin = () =>
  instance.get(
    `${kakaoRequestData.kakaoUrl}authorize?client_id=${kakaoRequestData.apiKey}&redirect_uri=${kakaoRequestData.redirectUrl}&response_type=code`,
  );

export { registerUser, loginUser, kakaoLogin };
